<template>
  <div id="scroll">
    <div class="flex mb">
      <div>房间号</div>
      <div>结算金额</div>
    </div>
    <div
      v-for="(item,i) in arrList"
      :key="i"
      class="flex bb"
      @click="goNext(item)"
    >
      <div>{{ item.roomSn }}</div>
      <div class="c_04">¥{{ Number(item.landLordBalanceAmount).toFixed(2) }}
        <van-icon
          color="#b2b2b2"
          name="arrow"
        />
      </div>
    </div>
    <div class="flex mt">
      <div>合计</div>
      <div>¥{{ Number(totlePrice).toFixed(2) }}</div>
    </div>
  </div>
</template>
<script>
import { GetbusinessLandlordBillRecordList, GetbusinessLandlordBillRecordMonthList } from '@/api/api_landlord';
export default {
  data() {
    return {
      arrList: [],
      totlePrice: null
    };
  },
  created() {
    this.$store.commit('setHeader', this.$route.query.billTime + '收款详情');
    this.getInfo()

  },
  methods: {
    getInfo() {
      let _api = this.$route.query.selected == '1' ? GetbusinessLandlordBillRecordList : GetbusinessLandlordBillRecordMonthList
      let data = {
        billTime: this.$route.query.billTime,
        landLordId: this.$route.query.landLordId,
      }
      _api(data).done(res => {
        this.arrList = []
        for (const k in res.data) {
          let obj = {}
          obj.id = k
          obj.roomSn = res.data[k].roomSn
          obj.landLordBalanceAmount = res.data[k].landLordBalanceAmount
          this.arrList.push(obj)
        }
        this.arrList.map(item => {
          this.totlePrice += item.landLordBalanceAmount
        })
      })
    },
    goNext(item) {
      this.$router.push({
        path: '/owner-finance-details',
        query: {
          billTime: this.$route.query.billTime,
          roomSn: item.roomSn,
          billId: item.id,
          selected: this.$route.query.selected,
          landlord: this.$route.query.landlord
        }
      });
    }

  }
};
</script>

<style scoped>
#scroll {
    padding: 15px 0;
    position: fixed;
    top: 50px;
    bottom: 0;
    left: 0;
    right: 0;
    overflow-y: scroll;
    background-color: #f6f6f6;
    font-size: 14px;
    font-weight: bold;
    height: calc(100% -90px);
    color: #000;
}
.flex {
    display: flex;
    justify-content: space-between;
    padding: 10px 20px;
    background: #fff;
}
.mb {
    margin-bottom: 10px;
}
.mt {
    margin-top: 10px;
}
.c_04 {
    color: #048fe5;
    display: flex;
    align-items: center;
}
.bb {
    border-bottom: 1px solid #e5e5e5;
}
</style>



